.DetailedInformation {
  width: auto;
  height: 100%;
}
.DetailedInformation .DetailedInformation-wrap {
  width: auto;
  height: 100%;
  overflow: auto;
}
.DetailedInformation .DetailedInformation-wrap .DetailedInformation-header {
  height: 80px;
  padding: 0 30px;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.DetailedInformation .DetailedInformation-wrap .DetailedInformation-header .DetailedInformation-header-title {
  font-size: 20px;
}
.DetailedInformation .DetailedInformation-wrap .DetailedInformation-line {
  height: 2px;
  background-color: #FB7299;
}
.DetailedInformation .DetailedInformation-wrap .DetailedInformation-main {
  width: auto;
