.DetailedInformation .DetailedInformation-wrap .DetailedInformation-main .DetailedInformation-item {
  width: auto;
  display: flex;
  flex-direction: row;
  padding: 20px 0 20px 20px;
  border-bottom: 1px solid #eee;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 10px;
}
.DetailedInformation .DetailedInformation-wrap .DetailedInformation-main .DetailedInformation-item .DetailedInformation-item-title {
  width: 120px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}
.DetailedInformation .DetailedInformation-wrap .DetailedInformation-main .DetailedInformation-img img {
  border: 1px solid #FB7299;
}
