.Borrow-wrap {
  width: auto;
  height: 100%;
  overflow: hidden;
}
.Borrow-wrap .Borrow {
  width: auto;
  height: 100%;
}
.Borrow-wrap .Borrow .Borrow-header-wrap {
  height: 100px;
  padding: 0 60px;
}
.Borrow-wrap .Borrow .Borrow-header-wrap .Borrow-header {
  height: 100px;
  width: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.Borrow-wrap .Borrow .Borrow-header-wrap .Borrow-header .Borrow-header-left {
  font-size: 20px;
}
.Borrow-wrap .Borrow .Borrow-line-wrap {
  height: 2px;
}
.Borrow-wrap .Borrow .Borrow-line-wrap .Borrow-line {
  height: 2px;
  background-color: #FB7299;
}
.Borrow-wrap .ant-btn-primary {
  background-color: #1890FF;
  border-color: #1890FF;
}
