.Customer-wrap {
  width: auto;
  height: 100%;
  overflow: hidden;
}
.Customer-wrap .Customer {
  width: 100%;
  height: 100%;
}
.Customer-wrap .Customer .Customer-header-wrap {
  height: 100px;
  width: auto;
  padding: 0 100px 0 40px;
}
.Customer-wrap .Customer .Customer-header-wrap .Customer-header {
  height: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.Customer-wrap .Customer .Customer-line-wrap {
  height: 2px;
}
.Customer-wrap .Customer .Customer-line-wrap .Customer-line {
  height: 2px;
  background-color: #6eb8d4;
}
.Customer-wrap .Customer .Customer-main {
  min-height: 630px;
}
.Customer-wrap .ant-btn-primary {
  background-color: #1890FF;
  border-color: #1890FF;
}
.Customer-wrap .site-card-wrapper {
  background: #ececec;
  padding: 30px;
}
.Customer-wrap .site-card-wrapper .ant-col {
  margin-bottom: 40px;
}
