.Home {
  display: flex;
  flex-direction: row;
}
.Home .ant-carousel {
  width: 1140px;
}
.Home .ant-carousel .slick-slide {
  text-align: center;
  height: 80%;
  line-height: 160px;
  background: #fff;
  overflow: hidden;
}
.Home .ant-carousel .slick-slide img {
  width: 1140px!important;
}
.Home .Home-main {
  overflow: auto;
}
.Home .Home-main p {
  text-indent: 2em;
}
