.LayoutHeader {
  color: #000;
  height: 140px;
  display: flex;
  flex-direction: column;
}
.LayoutHeader .ant-btn-primary {
  background-color: #1890FF;
  border-color: #1890FF;
}
.LayoutHeader .LayoutHeader-top {
  height: 49.5px;
  padding-right: 30px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  align-items: flex-end;
}
.LayoutHeader .LayoutHeader-top .LayoutHeader-top-content {
  display: flex;
  align-items: flex-end;
}
.LayoutHeader .LayoutHeader-top .LayoutHeader-top-content .LayoutHeader-top-content-desc {
  line-height: 350%;
  margin-right: 10px;
}
.LayoutHeader .LayoutHeader-top .LayoutHeader-top-content .LayoutHeader-top-content-logout {
  color: skyblue;
}
.LayoutHeader .LayoutHeader-line {
  height: 1px;
  background-color: black;
}
.LayoutHeader .LayoutHeader-bottom {
  height: 49.5px;
  background-color: #fff;
  line-height: 49.5px;
  display: flex;
  padding: 0 30px;
  flex-direction: row;
  justify-content: space-between;
}
.LayoutHeader .LayoutHeader-bottom .LayoutHeader-bottom-content,
.LayoutHeader .LayoutHeader-bottom .LayoutHeader-bottom-title {
  display: flex;
  flex-direction: row;
}
.LayoutHeader .LayoutHeader-bottom .LayoutHeader-bottom-title {
  font-size: 30px;
}
.LayoutHeader .LayoutHeader-bottom .LayoutHeader-bottom-content {
  display: flex;
}
.LayoutHeader .LayoutHeader-bottom .LayoutHeader-bottom-content .LayoutHeader-bottom-content-time,
.LayoutHeader .LayoutHeader-bottom .LayoutHeader-bottom-content .LayoutHeader-bottom-content-weather {
  margin-right: 5px;
}
.LayoutHeader .LayoutHeader-triangle {
  height: 40px;
  padding: 0 30px;
  position: relative;
}
.LayoutHeader .LayoutHeader-triangle .anticon-caret-down {
  position: absolute;
  top: -19px;
  left: 30px;
  width: 60px;
  height: 60px;
  font-size: 60px;
}
.LayoutHeader .LayoutHeader-triangle .anticon-caret-down path {
  color: #fff;
}
