.Role {
  height: 100%;
  overflow: hidden;
}
.Role .ant-btn-primary {
  background-color: #1890FF;
  border-color: #1890FF;
}
.Role .Role-wrap {
  height: 100%;
  overflow: auto;
}
.Role .Role-header-wrap {
  height: 80px;
  padding: 0 60px;
}
.Role .Role-header-wrap .Role-header {
  font-size: 30px;
  line-height: 80px;
}
.Role .Role-line-wrap {
  height: 2px;
}
.Role .Role-line-wrap .Role-line {
  height: 2px;
  background-color: #FB7299;
}
.Role .Role-main {
  padding: 10px 0;
}
