.category {
  height: 100%;
  width: 100%;
  background-color: #fff;
}
.category .ant-btn-primary {
  background-color: #1890FF;
  border-color: #1890FF;
}
.category .category-header {
  width: 100%;
  box-sizing: border-box;
  height: 105px;
  background-color: #fff;
  padding: 0 60px 0 30px;
}
.category .category-header .category-header-wrap {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  justify-items: center;
}
.category .category-header .category-header-wrap .category-header-list {
  height: 100%;
}
.category .category-header .category-header-wrap .category-header-list .category-header-list-active {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  justify-items: center;
}
.category .category-header .category-header-wrap .category-header-list .category-header-list-active button span {
  font-size: 14px;
}
.category .category-header .category-header-wrap .category-header-list .category-header-list-static {
  font-size: 20px;
  font-weight: 500;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  justify-items: center;
}
.category .category-header .category-header-wrap .category-header-add {
  color: red;
}
.category .category-line-wrap {
  height: 2px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.category .category-line-wrap .category-line {
  width: 100%;
  height: 2px;
  background-color: #eee;
}
.category .category-content {
  height: 582px;
  padding: 20px;
}
.category .category-content .ant-table-row .ant-table-cell:nth-child(1) {
  min-width: 200px;
}
.category .category-content .ant-table-row .ant-table-cell:nth-child(2) {
  width: 300px;
}
.selectIt {
  width: 100% !important;
}
.selectIt .ant-select-selector {
  width: 100% !important;
}
.selectIt .ant-select-selector .ant-select-selection-item {
  width: 100% !important;
}
